.not-found {
  background: url("./images/404.jpg") 50% no-repeat;
  background-size: cover;
  height: 100vh;
  position: relative; }
  .not-found .explanation {
    font: 24px "RaleExtra", sans-serif;
    margin: 0 auto;
    position: absolute;
    left: 50%;
    bottom: 30%;
    transform: translateX(-50%);
    color: #fff; }
  .not-found .button-wrap {
    position: absolute;
    left: 80%;
    bottom: 15%; }

@media (min-width: 768px) and (max-width: 991px) {
  .not-found .button-wrap {
    left: 70%; } }
@media (min-width: 451px) and (max-width: 767px) {
  .not-found .button-wrap {
    left: 50%;
    transform: translateX(-50%);
    bottom: 10%; }

  .button_earth {
    padding: 12px 20px;
    font: 13px "RaleExtra", sans-serif; }

  .not-found .explanation {
    font: 20px "RaleExtra", sans-serif;
    bottom: 20%; } }
@media (max-width: 450px) {
  .not-found .button-wrap {
    left: 50%;
    transform: translateX(-50%);
    bottom: 10%; }

  .button_earth {
    padding: 12px 20px;
    font: 13px "RaleExtra", sans-serif; }

  .not-found .explanation {
    font: 18px "RaleExtra", sans-serif;
    bottom: 20%; } }

