.welcome .slick-slider {
  color: #fff;
  text-align: center;
  padding-bottom: 330px; }
  .welcome .slick-slider .slider__secondaryHeader {
    font: 34px "RaleMedium", sans-serif;
    padding-bottom: 15px; }
  .welcome .slick-slider .slider__mainHeader {
    font: 56px "RaleExtra", sans-serif;
    padding-bottom: 30px; }
  .welcome .slick-slider .slider__text {
    font: 16px "MontSerratLight", sans-serif;
    padding: 0 350px 50px 350px; }
  .welcome .slick-slider .slick-next {
    top: 25%; }
    .welcome .slick-slider .slick-next:before {
      content: '';
      display: block;
      opacity: .5;
      width: 40px;
      height: 73px;
      background: url("./images/right.png") 50% no-repeat;
      transition: .3s all linear; }
    .welcome .slick-slider .slick-next:hover:before {
      opacity: .75; }
  .welcome .slick-slider .slick-prev {
    top: 25%; }
    .welcome .slick-slider .slick-prev:before {
      content: '';
      display: block;
      width: 40px;
      height: 73px;
      background: url("./images/left.png") 50% no-repeat;
      transition: .2s all linear; }
  .welcome .slick-slider .slick-dots {
    bottom: 50px; }
    .welcome .slick-slider .slick-dots li {
      width: 11px;
      height: 11px; }
    .welcome .slick-slider .slick-dots button {
      width: 11px;
      height: 11px;
      background: #fff;
      opacity: .5;
      border-radius: 50%;
      transition: .15s all linear; }
      .welcome .slick-slider .slick-dots button:before {
        content: ''; }
    .welcome .slick-slider .slick-dots .slick-active button {
      width: 13px;
      height: 13px;
      background: #000;
      border-radius: 50%;
      border: 2px solid #d3c1ad;
      opacity: .8; }

